.RandomRecipe-div {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.RandomRecipe-link:link {
  color: #b54747;
}

.RandomRecipe-link:visited {
  color: #b54747;
}

.RandomRecipe-link:hover {
  color: yellow;
}
